<template>
	<div id="home-setting">
		<public-header title="设置"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell to="" title="头像">
					<div class="flex c-gray" slot="right-icon">
						<div class="user-img">
							<img :src="delivery.avatar" alt="">
						</div>
					</div>
				</van-cell>
				<van-cell to="" title="我的二维码">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="手机号">
					<div class="flex c-gray" slot="right-icon">
						<span>{{delivery.mobile}}</span>
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="健康证">
					<div class="flex c-gray" slot="right-icon">
						<span>未上传</span>
						<van-icon name="arrow" class="margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="工作城市">
					<div class="flex c-gray" slot="right-icon">
						<span>太原</span>
						<van-icon name="arrow" class="margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="详细资料">
					<div class="flex" slot="right-icon">
						<span class="c-warning">填资料 让家人安心</span>
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell to="" title="本机号码">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell :to="util.getUrl({path: '/pages/member/phonic'})" title="语音设置">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="快捷消息管理">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="清理聊天缓存">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell to="" title="反馈">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell to="" title="管理细则">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="诊断">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
				<van-cell to="" title="关于美团众包">
					<div class="flex" slot="right-icon">
						<van-icon name="arrow" class="c-gray margin-5-l"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="login-out van-hairline--top van-hairline--bottom" @click="onLoginOut">退出登陆</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				delivery: {},
				showPreLoading: false
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					url: 'delivery/home/setting/list',
					vue: that,
					success: function(res) {
						that.delivery = res.deliveryer;
					}
				});
			},
			onLoginOut() {
				var that = this;
				that.$dialog.confirm({
					title: '温馨提示',
					message: '确定退出登录吗？'
				}).then(() => {
					this.util.removeStorage('deliveryerInfo');
					this.util.removeStorage('idtoken');
					this.$router.replace(this.util.getUrl({path: '/pages/auth/login'}));
					return;
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#home-setting .van-cell .van-cell__title{
		display: flex;
		align-items: center;
	}
	#home-setting .van-cell .user-img{
		width: 55px;
		height: 55px;
		border-radius: 55px;
		border: 1px solid #e0e0e0;
	}
	#home-setting .van-cell .user-img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#home-setting .login-out{
		margin: 20px 0;
		padding: 15px 0;
		text-align: center;
		font-size: 16px;
		font-weight: bold;
		background-color: #fff;
	}
</style>


